.wp-admin {
  &.toplevel_page_crm_dashboard {
    #wpcontent {
      transition: all .7s ease;
      @include mq($from: 750px) {
        margin-left: 140px;
      }
      #wpbody-content {
        min-height: 100vh;
        > div {
          display: none;
        }
        .clear {
          display: block;
        }
        .dashboard__body-start {
          display: block;
          margin: 10px;
          .card-block.card-height {
            max-width: unset;
          }
          .notice, .notice ~ .updated  {
            display: none;
          }
          .card-full-width {
            width: 100%;
            max-width: unset;
            padding: unset;
          }
          .profile-img-edit {
            width: fit-content;
            margin: auto;
          }
        }
      }
    }
    // , .folded #wpfooter
    &.folded #wpcontent {
      margin-left: 36px;
    }
    &.fwp-full-screen {
      #wpadminbar, #adminmenumain {
        display: none;
      }
      #wpcontent, #wpfooter {
        margin-left: auto;
      }
      #wpcontent {
        background: #ddd;
        z-index: -1;
        display: block;
        position: relative;
        padding-left: unset;
      }
    }
  }
}
.iq-banner ~ #wpbody #wpbody-content .dashboard__body-start #page_layout {
  max-width: unset;
  padding: unset;
  margin: auto;
  margin-top: -8rem;
}
.popup-action-menu {
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  display: block;
  position: absolute;
  background: #33333338;
  // vertical-align: middle;
  .popup-action-wrap {
    margin: auto;
    display: block;
    position: relative;
    top: calc(50% - 50px);
    .list {
      padding: 5px;
      margin: auto;
      margin-top: 10px;
      .list-item {
        cursor: pointer;
        &:hover {
          border-radius: 5px;
          background: #f0f0f1;
        }
      }
    }
  }
}
.profile-img-edit input.file-upload.profile-image-upload {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: #333;
  opacity: 0;
  visibility: visible;
  display: block;
}

.wp-admin {
  .acfgpt3-openpopup {
    /* background: url("https://png.pngtree.com/thumb_back/fh260/background/20190221/ourmid/pngtree-ai-artificial-intelligence-technology-concise-image_19646.jpg") center center no-repeat rgb(255, 255, 255); */
    background-size: cover;
    padding: 10px 20px;
    color: #333;
    text-transform: uppercase;
    -webkit-text-stroke: .5px #333;
    font-weight: bold;
  }
  form#acfgpt3_popupform {
    text-align: left;
    select, input {
      border-radius: 5px;
      padding: 5px 15px;
    }
    label {
      font-size: 18px;
      font-family: 'Roboto';
      text-transform: uppercase;
      text-align: left;
      margin: unset;
      margin-bottom: 5px;
      margin-top: 15px;
    }
  }
  form {
    label {
      overflow: hidden;
      position: relative;
      input {
        &[type="checkbox"] {
          top: 20px;
          right: 10px;
          display: block;
          position: absolute;
        }
      }
    }
  }
  #aigpt3-acf-field-submitbox {
    margin: auto;
    padding: 6px 0;
    margin-left: 10px;
    .score-text {
      #aigpt3_scheduled_on {
        display: none;
        &.should_open {
          display: block;
        }
      }
    }
  }
  #acfgpt3_resform {
    text-align: left;
    .single_block {
      .text {
        border: none;
        margin-top: 1rem;
        padding-top: 1rem;
        border-top: 2px solid #d5d5d5;
      }
      .d-none + .text {
        border: none;
        margin-top: 0;
      }
    }
  }
  #acfgpt3_popupimgs {
    .input-fields {
      margin: auto;
      display: flex;
      flex-wrap: wrap;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
      justify-content: space-between;
    }
    .single_block {
      margin: auto;
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }
    .gallery-fields {
      justify-content: space-around;
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      label {
        border-radius: 5px;
        box-shadow: 2px 2px 4px;
      }
    }
  }
}


// SETTINGS PAGE
.settings-container {
  code {
    -moz-user-select: all;
    -webkit-user-select: all;
    user-select: all;
  }
  table tbody tr td {
    input:not([type="checkbox"]):not([type="radio"]), select, textarea {
      width: auto;
      display: block;
      max-width: unset;
      width: 100%;
      height: 40px;
    }
    textarea {
      height: 300px;
    }
  }
}